@import url(https://fonts.googleapis.com/css?family=Lato);
.pokecard{
    width: 270px;
    margin: 0.2em 0;
    border-radius: 8px;
    text-align: center;
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
    background-color: #fff;
}

.pokecard-name{
    font-size: 1.8em;
}

.pokecard-container-img{
    width: 90%;
    margin: auto;
    background-color: #94A7A7;
}

.pokecard-img{
    width: 150px;
    margin: 5px  0;
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
}

.pokecard-img:hover{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    opacity: 0.8;
}

.pokecard-info{
    font-size: 1em;
    margin: 0.2em 0;
}
.pokedex{
    text-align: center;
}

.pokedex-container{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 1em;
}

.pokedex-info{
    margin: 0.2em 0;
}

.pokedex-status{
    font-size: 1.8em;
}

.pokedex-exp{
    font-size: 1.5em;
}
.pokegame{
    margin: 0;
}
body{
    background-color: #94A7A7;
    font-family: 'Lato', sans-serif;
}

